import { CButton, CCard, CCardBody, CCol, CForm, CFormInput, CFormLabel, CFormSelect, CFormTextarea, CInputGroup, CInputGroupText, CRow } from '@coreui/react'
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify'

const CreateBrand = () => {
  const [error, setError] = useState({});
  const [brandCategory, setBrandCategory]=useState([]);
  const Navigate =  useNavigate();
  const [validated, setValidated] = useState(false);
  const [selectedImage, setSelectedImage] = useState({});


  const handleSubmit = (e) => {
    e.preventDefault();
    const form = e.currentTarget
    if (form.checkValidity() === false) {     
      e.stopPropagation()
      toast.error("Please fill up required fields.")
      setValidated(true)
      return;
    }
    setValidated(false); 
    const [brand_name, brand_category, brand_description, brand_url, image,  status] = e.target;


  var requestOptions = {
    method: 'POST',
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify({
      "brand_name": brand_name.value,
      "brand_category": brand_category.value,
      "brand_description": brand_description.value,
      "image": selectedImage._id,
      "url": brand_url.value,
      "status":  parseInt(status.value)
    }),
  };

  fetch(process.env.REACT_APP_API_URL+"brand/create", requestOptions)
  .then(response => response.json())
  .then(result => {
    if(result.status==200){
      toast.success(result.message);
      setTimeout(()=>{
        Navigate("/brands")
      }, 5000);

    }else{
      toast.error(result.message);
    }
  })
  .catch(error => console.log('error', error));    

  }

  const handleImageUpload = (fileInput) => {
    var formdata = new FormData();
    formdata.append("image", fileInput.files[0], fileInput.files[0].name);
    formdata.append("type", "image");

    var requestOptions = {
      method: 'POST',
      body: formdata,
      redirect: 'follow'
    };

    fetch(process.env.REACT_APP_API_URL + "images/add", requestOptions)
      .then(response => response.json())
      .then(result => {console.log(result); setSelectedImage(result.data)})
      .catch(error => console.log('error', error));
  };



  useEffect(() => {
    async function fetchData() {
      try {
        var requestOptions = {
          method: 'POST',
          redirect: 'follow'
        };
  
        let url = process.env.REACT_APP_API_URL + "brand-category/list";
        let response = await fetch(url, requestOptions);
        let result = await response.json();
        console.log(result.data);
        setBrandCategory(result.data);
      } catch (error) {
        console.log('error', error);
      }
    }
  
    fetchData();
  }, []);

  return (
    <>
      <ToastContainer />
      <h1 className='headingType1  text-center mb-4'>Add New Product</h1>
      <CCard >            
          <CCardBody className='p-5'>
              
              <CRow className='justify-content-center'>
                <CCol lg={8}>
                  <CForm
                  className="row g-3 needs-validation"
                  noValidate
                  validated={validated}
                  onSubmit={handleSubmit}
                >
                  <CCol md={6}>
                    <CFormInput
                      type="text"
                      defaultValue=""
                      feedbackInvalid="Please enter a Brand Name."
                      id="validationCustom21"
                      label="Brand name"
                      name='brand_name'
                      required
                    />
                  </CCol>
                  <CCol md={6}>
                    <CFormSelect
                      type="text"
                      defaultValue=""
                      feedbackInvalid="Please Select Status."
                      id="validationCustom22"
                      label="Brand Category"
                      name='brand_category'
                      required
                    >
                      <option value={''}>Select Brand Category</option>
                      {brandCategory.map((item,key)=>(
                        <option key={key} value={item._id}>{item.brand_category_name}</option>
                        ))}
                    </CFormSelect>
                  </CCol>                
                 
                  
                  <CCol md={12}>
                    <CFormLabel htmlFor="description">Description</CFormLabel>
                    <CFormTextarea
                      type="text"
                      aria-describedby="inputGroupPrependFeedback"                      
                      name='brand_description'
                      id="description"                     
                    />                    
                  </CCol>
                  <CCol md={12}>
                    <CFormInput
                        type="text"
                        defaultValue=""
                        feedbackInvalid="Please enter a Brand Name."
                        id="validationCustom21"
                        label="Brand url"
                        name='brand_url'
                        required
                      />               
                  </CCol>
                  <CCol md={6}>
                    <CFormInput
                      type='file'
                      name='uploadImage'
                      label="Upload Image"
                      onChange={(i)=>handleImageUpload(i.target)}
                    >
                    </CFormInput>
                  </CCol>
                  <CCol md={6}>
                    <CFormSelect
                      type="text"
                      defaultValue=""
                      feedbackInvalid="Please Select Status."
                      id="validationCustom23"
                      label="Status"
                      name='status'
                      required
                    >
                      <option value={"1"}>Active</option>
                      <option value={"0"}>Inactive</option>
                    </CFormSelect>
                  </CCol>
                  <CCol xs={12}  className='text-center'>
                    <CButton color="primary" type="submit" className='py-2 px-5'>
                      Submit form
                    </CButton>
                  </CCol>

                </CForm>
                </CCol>
              </CRow>






              




              
              
          </CCardBody>
      </CCard>
    </>
  )
}

export default CreateBrand
